:root {
  // base
  --font-family: "OpenSans", sans-serif;
  --content-width: 1650px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors

  --light-color: #ffffff;
  --main-color: #494B68;
  --accent-color: #31C1DE;
  --bg-color: #061628;
}
