@import "vars";
@import "mixins";
@import "fonts";
@import "settings";


.all_pages {
  position: fixed;
  top: 20%;
  right: 0;
  z-index: 101;
  &:hover {
    ul {
      right: 0;
    }
  }
  .page_point {
    padding: 10px 15px;
    background: #00d2ff;
    cursor: pointer;
    color: #fff;
  }
  ul {
    background: #00d2ff;
    padding: 15px 20px;
    position: absolute;
    top: 100%;
    width: 200px;
    right: -200px;
    transition: 0.4s;
    max-height: 450px;
    overflow-y: auto;
    li {
      list-style: none;
      margin-bottom: 15px;
      font-size: 16px;
      a {
        color: #fff;
      }
    }
  }
}

html::-webkit-scrollbar {
  width: 0;
  scrollbar-width: none;
}
html {
  scrollbar-width: none;
}

body {
  &.lock {
    overflow: hidden;
  }
}

.wow {
  visibility: hidden;
}

// Header start ========================================================================================================================================================

.header {
  padding: 20px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  margin: 0 auto;
  transition: all .4s ease 0s;
  @media (max-width: 992px){
      padding: 10px 0;
  }
  &.scroll {
    background-color: var(--bg-color);
    box-shadow: 2px 2px 0px 0px rgba(#31C1DE, 0.2);
    padding: 10px 0;
    .header__logo {
      img {
        transition: all 0.3s ease 0s;
      }
    }
  }

  // .header__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 101;
    @media (max-width: 768px){
        svg {
          width: 150px;
          height: auto;
        }
    }
    @media (max-width: 480px){
      svg {
        width: 100px;
        height: auto;
      }
    }
  }

  // .header__navigation

  &__navigation {
    display: flex;
    align-items: center;
    column-gap: 80px;
    @media (max-width: 992px){
      position: fixed;
      left: 0;
      top: 0;
      z-index: 100;
      overflow-y: auto;
      width: 100%;
      height: 100vh;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      row-gap: 25px;
      transition: all 0.3s ease 0s;
      transform: translate(calc(100% - 70px), calc(-100% + 100px));
      background: linear-gradient(145deg, var(--bg-color) 10%, var(--accent-color));
      border-bottom-left-radius: 35px;
      @media (max-width: 768px){
        transform: translate(calc(100% - 70px), calc(-100% + 80px));
      }
      @media (max-width: 480px){
        transform: translate(calc(100% - 70px), calc(-100% + 70px));
      }
      &.menu--active {
        transform: translate(0%, 0%);
        border-bottom-left-radius: 0px;
      }
    }
    @media (max-width: 1200px){
      column-gap: 30px;
    }
    nav {
      > ul {
        display: flex;
        column-gap: 30px;
        @media (max-width: 1200px){
            column-gap: 15px;
        }
        @media (max-width: 992px){
            flex-direction: column;
            align-items: center;
            row-gap: 5px;
        }
        > li {
          > a {
            color: var(--light-color);
            font-family: 'Astoria';
            font-size: 18px;
            font-style: normal;
            font-weight: 400;
            line-height: 30px;
            transition: all 0.3s ease 0s;
            @media (min-width: 993px){
                &.active {
                  color: var(--accent-color);
              }
            }
            @media (max-width: 992px){
                font-size: 22px;
            }
            @media (min-width: 993px){
                &:hover {
                  color: var(--accent-color);
                }
            }
          }
        }
        >li:nth-child(2) {
          &.open {
            @media (max-width: 992px){
              &.open {
                > ul {
                  display: block;
              }
            }
            &:after {
              right: -6px !important;
              transform: rotate(225deg) translateX(-4px);
            }
          }
        }
        }
        >li:nth-child(3), >li:nth-child(2)   {
          position: relative;
          @media (max-width: 992px){
            transform: translateX(-12px);
            > ul {
              transform: translateX(12px);
            }
              &:after {
                content: '';
                position: absolute;
                right: -15px;
                top: 14px;
                width: 6px;
                height: 6px;
                border: 2px solid var(--light-color);
                border-top: 0;
                border-left: 0;
                transform: rotate(45deg);
              }
              &.open {
                @media (max-width: 992px){
                  &.open {
                    > ul {
                      display: block;
                  }
                }
                &:after {
                  right: 40px;
                  transform: rotate(225deg) translateX(-4px);
                }
              }
            }
          }
          @media (max-width: 992px){
              text-align: center;
          }
          @media (min-width: 993px){
            &:hover {
              > ul {
                opacity: 1;
                visibility: visible;
              }
            }
          }
          > ul {
            transition: all 0.3s ease 0s;
            position: absolute;
            top: 100%;
            left: 0;
            z-index: 101;
            width: max-content;
            padding: 20px 25px;
            opacity: 0;
            visibility: hidden;
            background-color: var(--light-color);
            > li {
              > a {
                color: var(--main-color);
                font-family: 'Astoria';
                font-size: 16px;
                font-style: normal;
                font-weight: 400;
                line-height: 30px;
                @media (max-width: 992px){
                    color: var(--light-color);
                    line-height: 130%;
                }
              }
            }
            @media (max-width: 992px){
                position: static;
                opacity: 1;
                visibility: visible;
                display: none;
                background-color: transparent;
                text-align: center;
                padding: 0;
                width: auto;
                height: auto;
                border-top: none;
            }
            > li {
              > a {
                transition: color 0.3s ease 0s;
                @media (min-width: 993px){
                  &:hover {
                    color: var(--accent-color);
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  // .header__languages

  &__languages {
    position: relative;
    &:hover {
      > ul {
        opacity: 1;
        visibility: visible;
      }
    }
    a {
      color: var(--light-color);
      font-family: 'Astoria';
      font-style: normal;
      font-weight: 400;
      line-height: 30px;
      text-transform: uppercase;
      transition: all 0.3s ease 0s;
      @media (min-width: 993px){
          &:hover {
            color: var(--accent-color);
          }
      }
    }
    > ul {
      position: absolute;
      top: 100%;
      left: -150%;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease 0s;
      @media (max-width: 992px){
          background-color: var(--light-color);
          left: calc(-150% + 7.5px);
          padding: 5px 8px;
          z-index: 101;
          border-radius: 4px;
          transform: translateX(-7px);
      }
    }
  }

  // .header__lang-list

  &__lang-list {
    background-color: var(--light-color);
    border-radius: 5px;
    padding: 5px 10px;
    display: flex;
    column-gap: 7px;
    > li {
      > a {
        color: var(--main-color);
        line-height: 18px;
      }
    }
  }

  // .header__btn-book

  &__btn-book {
    @media (max-width: 992px){
        position: absolute;
        bottom: 14%;
    }
  }

  // .header__btn

  &__btn {
    padding: 8px 30px;
    font-size: 18px;
    @media (max-width: 1200px){
        padding: 8px 20px;
    }
  }

  .burger {
    @include burger;

    display: none;
    z-index: 101;
    @media (max-width: 992px){
        display: block;
    }
  }
}

// Section hero start ========================================================================================================================================================

@keyframes slideAnime {
  from {
    width: 100%;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  }
  50% {
    width: 100%;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  }
  100% {
    width: 54vw;
  }
}

.hero {
  position: relative;
  max-height: 1080px;

  // .hero__slide

  &__slide {
    position: relative;
    z-index: 100;
    height: 100% !important;
    @media (max-width: 1650px){
        height: auto !important;
    }
    &.swiper-slide-active {
      &:after {
        animation: slideAnime 0.8s linear 0s;
      }
    }
    @media (max-width: 576px){
        height: auto;
    }
    &:after {
      content: '';
      position: fixed;
      width: 54vw;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(#061628, 0.75);
      clip-path: polygon(0 0, 55% 0, 100% 100%, 0% 100%);
      z-index: 99;
      @media (max-width: 576px){
        content: none;
      }
    }
    &:before {
      content: '';
      position: fixed;
      width: 59.9vw;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(#061628, 0.75);
      clip-path: polygon(0 0, 65% 0, 100% 100%, 0% 100%);
      z-index: 99;
      transition: all 0.3s ease 0s;
      @media (max-width: 576px){
          width: 100%;
          height: 100%;
          background-color: rgba(#061628, 0.4);
          clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
      }
    }
    ._container {
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      position: relative;
      z-index: 100;
    }
    &-wrap {
      padding-top: 260px;
      padding-bottom: 220px;
      max-width: 630px;
      display: flex;
      padding-left: 15px;
      align-items: flex-start;
      flex-direction: column;
      justify-content: space-between;
      max-height: 600px;
      position: relative;
      z-index: 100;
      @media (max-width: 1650px){
        padding-left: 0;
        max-width: 800px;
        padding-top: 250px;
        padding-bottom: 150px;
      }
      @media (max-width: 576px){
          padding-top: 120px;
          padding-bottom: 150px;
      }

    }
    > img {
      width: 100%;
      height: auto;
      position: absolute;
      z-index: -1;
      object-fit: cover;
      height: 100%;
    }
  }

  // .hero__title

  &__title {
    margin-bottom: 40px;
    font-size: 48px;
    font-style: normal;
    line-height: 60px;

    @media (max-width: 1650px){
        margin-bottom: 25px;
        font-size: 40px;
        line-height: 55px;
        br {
          display: none;
      }
    }
    @media (max-width: 992px){
        font-size: 32px;
        line-height: 48px;
    }
    @media (max-width: 480px){
        font-size: 26px;
        line-height: 36px;
    }
  }

  // .hero__subtitle

  &__subtitle {
    color: var(--light-color);
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: 40px;
    margin-bottom: 60px;
    @media (max-width: 1650px){
        font-size: 22px;
        margin-bottom: 40px;
    }
    @media (max-width: 992px){
        font-size: 20px;
        line-height: 130%;
    }
    @media (max-width: 480px){
        font-size: 16px;
    }
  }

  // .hero__btn

  &__btn {
    padding: 15px 95px;
    font-size: 20px;
    @media (max-width: 992px){
        padding: 10px 60px;
    }
    @media (max-width: 480px){
        padding: 10px 0;
        width: 100%;
    }
  }
  .swiper {
    max-height: 1080px;
  }
  .swiper-slide {
    min-height: 1080px;
    @media (max-width: 1650px){
        min-height: auto;
        height: 100%;
    }
  }
  .swiper-pagination {
    text-align: right;
    padding-right: 13%;
    bottom: 20%;
    @media (max-width: 1650px){
        bottom: 13%;
    }
    @media (max-width: 992px){
        padding-right: 2%;
        bottom: 10%;
    }

    @media (max-width: 480px){
        text-align: center;
    }
  }
  .swiper-pagination-bullet {
    opacity: 1;
    width: 40px;
    height: 5px;
    background-color: rgba(#fff, 0.2);
    border-radius: 0;
    &:not(:last-child) {
      margin-right: 6px;
    }
  }
  .swiper-pagination-bullet-active {
    width: 80px;
    background-color: rgba(#fff, 1);
  }
}

// Section hero end ========================================================================================================================================================

// Section welcome start ========================================================================================================================================================

.welcome {
  background-position: left 50%;
  background-repeat: no-repeat;
  background-size: 65vw 100%;
  padding-top: 150px;
  padding-bottom: 170px;
  @media (max-width: 1450px){
      background-color: rgba(#31C1DE, 0.7);
  }
  @media (max-width: 1200px){
      background-image: none !important;
      background-color: #fff ;
      padding-top: 80px;
      padding-bottom: 80px;
  }

  // .welcome__wrap

  &__wrap {
    display: flex;
    @media (max-width: 1200px){
      flex-direction: column-reverse;
      row-gap: 45px;
    }
  }

  // .welcome__list

  &__list {
    flex: 0 1 calc(100% - 530px);
    @media (max-width: 1200px){
        flex: 0 1 100%;
    }
  }

  // .welcome__items

  &__items {
    max-width: 630px;
    display: flex;
    flex-wrap: wrap;
    column-gap: 30px;
    row-gap: 30px;
    @media (max-width: 1450px){
        max-width: 580px;
    }
    @media (max-width: 1200px){
        background-color: var(--accent-color);
        padding: 30px;
        max-width: none;
    }
    @media (max-width: 576px){
        padding: 30px 15px;
        column-gap: 15px;
        row-gap: 15px;
        justify-content: center;
    }
  }

  // .welcome__item

  &__item {
    flex: 0 1 calc(50% - 15px);
    padding: 40px 60px;
    border: 1px solid rgba(255, 255, 255, 0.30);
    text-align: center;
    transition: all 0.3s ease 0s;
    @media (max-width: 1450px){
        padding: 20px;
    }
    @media (max-width: 480px){
      flex: 0 1 100%;
    }
    &:hover {
      box-shadow: 0 0 15px 2px rgba(255, 255, 255, 0.60);
      h4,.welcome__numbers, .welcome__descr {
        color: var(--main-color);
      }
      svg path {
        fill: var(--main-color);
      }
    }
    &:nth-child(even) {
      transform: translateY(30px);
      @media (max-width: 1200px){
        transform: translateY(0px);
      }
    }
    h4 {
      color: var(--light-color);
      font-family: 'Astoria';
      font-size: 30px;
      font-style: normal;
      font-weight: 400;
      line-height: 30px;
      margin-bottom: 20px;
      transition: all 0.3s ease 0s;
    }
    svg {
      margin-bottom: 20px;
      path {
        transition: all 0.3s ease 0s;
        opacity: 1;
      }
    }
  }

  // .welcome__numbers

  &__numbers {
    color: var(--light-color);
    font-family: 'Astoria';
    font-size: 40px;
    font-style: normal;
    font-weight: 100;
    line-height: 50px;
    transition: all 0.3s ease 0s;
  }

  // .welcome__descr

  &__descr {
    color: var(--light-color);
    font-family: 'Astoria';
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    transition: all 0.3s ease 0s;
  }

  // .welcome__description

  &__description {
    flex: 0 1 530px;
    @media (max-width: 1550px){
        align-self: flex-end;
    }
    @media (max-width: 1200px){
        flex: 0 1 100%;
    }
    h3 {
      color: var(--accent-color);
      text-align: right;
      font-family: 'Astoria';
      font-size: 70px;
      font-style: normal;
      font-weight: 400;
      line-height: 100%;
      @media (max-width: 1450px){
        color: var(--light-color);
      }
      @media (max-width: 1200px){
        color: var(--accent-color);
        text-align: left;
      }
      @media (max-width: 992px){
          font-size: 55px;
      }
      @media (max-width: 480px){
        font-size: 44px;
      }
      span {
        color: var(--main-color);
        font-family: 'Astoria';
        font-size: 40px;
        font-style: normal;
        font-weight: 400;
        display: inline-block;
        transform: translateY(-25px);
        @media (max-width: 1450px){
          color: var(--light-color);
        }
        @media (max-width: 1200px){
          color: var(--main-color);
        }
        @media (max-width: 992px){
          transform: translateY(-15px);
        }
        @media (max-width: 480px){
          transform: translateY(-10px);
          font-size: 28px;
          margin-bottom: 15px;
        }
      }
    }
  }

  // .welcome__text

  &__text {
    color: var(--main-color);
    text-align: right;
    font-family: 'OpenSans';
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    margin-bottom: 60px;
    @media (max-width: 1450px){
      color: var(--light-color);
    }
    @media (max-width: 1200px){
      color: var(--main-color);
      text-align: left;
    }
    @media (max-width: 992px){
        margin-bottom: 35px;
    }
  }

  // .welcome__btn

  &__btn {
    padding: 8px 30px;
    font-size: 18px;
    column-gap: 15px;
    transition: color 0.3s ease 0s;
    svg {
      transition: all 0.3s ease 0s;
      width: 16px;
      height: 16px;
    }
    svg path {
      transition: all 0.3s ease 0s;
    }
    @media (min-width: 993px){
        &:hover {
          color: var(--light-color);
          svg {
            transform: translateX(5px);
          }
          svg path {
            fill: var(--light-color);
          }
        }
    }
    @media (max-width: 480px){
        width: 100%;
        padding: 8px 0;
    }
  }
}

// Section welcome end ========================================================================================================================================================

// Section Our Services start ========================================================================================================================================================

.our-services {
  position: relative;
  background-color: var(--bg-color);
  padding-top: 120px;
  padding-bottom: 122px;
  @media (max-width: 768px){
      padding-top: 80px;
      padding-bottom: 80px;
  }
  ._container {
    position: relative;
    z-index: 2;
  }
  &:after {
    content: '';
    position: absolute;
    width: 34.8vw;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(#0A2746, 1);
    clip-path: polygon(0 0, 0% 85%, 98% 0);
    z-index: 1;
    @media (max-width: 576px){
      content: none;
    }
  }
  &:before {
    content: '';
    position: absolute;
    width: 46.4vw;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(#081F37, 1);
    clip-path: polygon(0 0, 97.8% 0%, 61% 100%, 0% 100%);
    z-index: 1;
    @media (max-width: 576px){
        width: 100%;
        height: 100%;
        background-color: rgba(#061628, 0.4);
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    }
  }

  // .our-services__title

  &__title {
    margin-bottom: 30px;
    text-align: center;
  }

  // .our-services__subtitle

  &__subtitle {
    max-width: 1370px;
    margin: 0 auto;
    color: var(--light-color);
    text-align: center;
    font-family: 'OpenSans';
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    margin-bottom: 60px;
  }

  // .our-services__items

  &__items {
    display: flex;
    column-gap: 2px;
    justify-content: space-between;
    @media (max-width: 1500px){
        flex-wrap: wrap;
        row-gap: 2px;
        justify-content: center;
    }
  }

  // .our-services__item

  &__item {
    background-color: #0C2F55;
    flex: 0 1 calc(20% - 2px);
    padding: 70px 40px 30px 40px;
    text-align: center;
    display: flex;
    flex-direction: column;
    transition: all 0.3s ease 0s;
    position: relative;
    @media (min-width: 993px){
      &:hover {
        box-shadow: 0 0 30px 10px rgba(#061628, 0.8);
        z-index: 99;
      }
    }
    &:after, &:before {
      content: '';
      position: absolute;
      left: 0;
      width: 100%;
      height: 0px;
      background-color: #0C2F55;
      transition: all 0.3s ease 0s;
    }
    &:after {
      top: 100%;
    }
    &:before {
      top: 0px;
    }
    @media (max-width: 1500px){
      flex: 0 1 calc(30% - 2px);
    }
    @media (max-width: 992px){
        flex: 0 1 calc(50% - 2px);
        padding: 40px 15px;
    }
    @media (max-width: 576px){
        flex: 0 1 100%;
        max-width: 328px;
    }
    svg path {
      transition: fill 0.2s ease 0s;
    }
    @media (min-width: 1501px){
      &:hover {
        &:after {
          height: 20px;
        }
        &:before {
          top: -20px;
          height: 20px;
        }
        .our-services__btn {
          opacity: 1;
          visibility: visible;
        }
        svg path {
          fill: var(--light-color);
        }
      }
    }
    &-wr {
      flex-grow: 1;
    }
    h4 {
      color: var(--light-color);
      font-family: 'Astoria';
      font-size: 24px;
      font-style: normal;
      font-weight: 400;
      line-height: 30px;
      min-height: 60px;
      max-width: 90%;
      margin: 0 auto;
      margin-bottom: 30px;
      text-align: center;
    }
  }

  // .our-services__img

  &__img {
    width: 80px;
    height: 80px;
    margin: 0 auto;
    margin-bottom: 30px;
    svg {
