.footer {
  background-color: #0D1219;

  // .footer__wrap

  &__wrap {
    padding-top: 60px;
    padding-bottom: 50px;
    border-bottom: 1px solid #494B68;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
    grid-auto-rows: 1fr;
    @media (max-width: 992px){
      grid-template-columns: repeat(2, 1fr);
      row-gap: 50px;
    }
    @media (max-width: 480px){
      grid-template-columns: repeat(1, 1fr);
      row-gap: 25px;
      grid-auto-rows: auto;
      text-align: center;
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }

  // .footer__block

  &__block {
    &:first-child {
      display: flex;
      flex-direction: column;
      row-gap: 35px;
      @media (max-width: 480px){
          align-items: center;
      }
      > ul {
        display: flex;
        column-gap: 10px;
        > li {
          width: 30px;
          height: 30px;
          &:hover {
            box-shadow: 1px 1px 15px 0 rgba(255,255,255, 0.5);
            > a {
              background-color: var(--accent-color);
            }
          }
          > a {
            transition: all 0.3s ease 0s;
            display: inline-block;
            width: 100%;
            height: 100%;
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }
        }
      }
    }
    &:not(:first-child) {
      > h6 {
        color: var(--accent-color);
        font-family: 'OpenSans';
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 30px;
        text-transform: uppercase;
        margin-bottom: 3px;
      }
      > ul {
        > li {
          color: var(--light-color);
          font-family: 'OpenSans';
          font-size: 14px;
          font-style: normal;
          font-weight: 400;
          line-height: 30px;
          address {
            font-style: normal;
          }
          > a {
            transition: all 0.3s ease 0s;
            color: var(--light-color);
            position: relative;
            &:after {
              content: '';
              position: absolute;
              left: 0;
              bottom: -2px;
              height: 1px;
              width: 0%;
              background-color: var(--light-color);
              transition: all 0.3s ease 0s;
            }
            &:hover:after {
              width: 100%;
            }
          }
        }
      }
    }
  }

  // .footer__logo

  &__logo {
    display: inline-block;
    width: 175px;
  }

  // .footer__copyright

  &__copyright {
    padding-top: 15px;
    padding-bottom: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--light-color);
    font-family: 'OpenSans';
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 30px;
    @media (max-width: 768px){
        flex-direction: column;
    }
    @media (max-width: 400px){
        font-size: 12px;
        line-height: 170%;
    }
    a {
      display: inline-block;
      margin-left: 15px;
      color: var(--light-color);
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

