html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}


.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 18px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
 // overflow: hidden; //     position: sticky -   
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

._container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn-outline-light, .btn-outline-dark {
  color: var(--light-color);
  font-family: 'Astoria';
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  border: 1px solid var(--light-color);
  transition: all 0.3s ease 0s;
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  column-gap: 20px;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: var(--accent-color);
    z-index: -1;
    transition: all 0.3s ease 0s;
  }
  img {
    transition: all 0.3s ease 0s;
  }
  @media (min-width: 993px){
      &:hover {
        &:after {
          width: 100%;
        }
        box-shadow: 0 0 20px 2px rgba(#fff, 0.3);
        img {
          transform: translateX(5px);
        }
    }
  }
}

.btn-outline-dark {
  color: var(--main-color);
  border: 1px solid var(--main-color);
  @media (min-width: 993px){
    &:hover {
      box-shadow: 0 0 20px 2px rgba(#494B68, 0.3);
    }
  }
}

.btn-accent {
  color: var(--light-color);
  font-family: 'Astoria';
  font-style: normal;
  font-weight: 400;
  line-height: 30px;
  background-color: var(--accent-color);
  display: inline-flex;
  align-items: center;
  transition: all 0.3s ease 0s;
  img {
    transition: transform 0.3s ease 0s;
  }
  &:hover {
    box-shadow: 0 0 15px 2px rgba(#31C1DE, 0.54);
    img {
      transform: translateX(5px);
    }
  }
}

.title-light, .title-dark {
  font-family: 'Astoria';
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  line-height: 60px;
  @media (max-width: 576px){
      font-size: 36px;
      line-height: 140%;
  }
}

.title-light {
  color: var(--light-color);
}

.title-dark {
  color: var(--main-color);
}

.position-right {
  text-align: right;
  @media (max-width: 1200px){
      text-align: left;
  }
}

.scroll-down {
  z-index: 1;
  position: absolute;
  right: 135px;
  bottom: 18%;
  width: 60px;
  height: 60px;
  border-radius: 30px;
  border: 1px solid rgba(255, 255, 255, 0.40);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  @media (max-width: 1650px){
      right: 50px;
      bottom: 10%;
  }
  @media (max-width: 1200px){
      right: auto;
      left: 2%;
      width: 40px;
      height: 40px;
  }
  @media (max-width: 992px){
      bottom: 7%;
  }
  @media (max-width: 576px){
      display: none;
  }
  img {
    width: 20px;
    height: 22px;
    @media (max-width: 1200px){
        width: 14px;
        height: 18px;
    }
  }
  @media (min-width: 993px){
    &:hover {
      box-shadow: 0 0 20px 2px rgba(#494B68, 0.9);
    }
  }
}

